import React from "react";
import {Link} from "react-router-dom";

// import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
// import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

function Header() {

  return (
    <div>
      <Navbar bg="light" expand="lg">
        <Container fluid>
          <Navbar.Brand href="/">Elirecruiter</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" className="shadow-none" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '250px' }}
              navbarScroll
            >
              <Nav.Link href="/">Home</Nav.Link>
              <Nav.Link href="/about">About</Nav.Link>
              <Nav.Link href="/service">Service</Nav.Link>
              <NavDropdown title="More" id="navbarScrollingDropdown">
                <NavDropdown.Item href="/hire">Hire</NavDropdown.Item>
                <NavDropdown.Item href="/blog">Blog</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="/blog">Career</NavDropdown.Item>
              </NavDropdown>
            </Nav>
            <Link to={"/account"} className="text-muted p-2">Account</Link>
            <Link to={"/signin"} className="text-muted p-2">Sign in</Link>
            <Link to={"/signup"} className="text-muted p-2">Sign up</Link>

            {/* <Form className="d-flex">
              <Form.Control
                type="search"
                placeholder="Search"
                className="me-2"
                aria-label="Search"
              />
              <Button variant="outline-success">Search</Button>
            </Form> */}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}
export default Header;